.maControlPanel-component {
    header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 9999;
        background: linear-gradient(to right, $pink, $dark);
        color: $white;
        width: 100%;
        padding: 10px 50px;
        span {
            float: right;
            color: $blue;
            font-family: $font-title;
            font-size: 0.6em;
            font-weight: normal; } }

    main {
        margin-top: 50px;
        display: flex;
        background: $white; }

    .col-left {
        width: 12vw;
        max-width: 300px;
        min-height: 100vh; }
    .col-right {
        width: 88vw;
        // max-width: 1200px
        min-height: 100vh;
        padding: 15px 15px; }

    //forms
    form {
        width: 100;
        margin: 0 auto;

        .form-control {
            margin: 10px 0;

            input {
                width: 50vw;
                padding: 5px;
                font-size: 1.2em;
                &[type="submit"] {
                    width: auto;
                    padding: 10px;
                    border-radius: 6px;
                    color: $white;
                    font-family: $font-title;
                    font-weight: 700;
                    cursor: pointer;
                    float: right;
                    @include btn-pink; } }

            select {
                padding: 5px;
                border-radius: 6px;
                min-width: 120px;
                option {
                    font-family: $font-title; } }

            .maTextEditor-component {
                margin-top: 25px; } }

        .form-control-inline {
            margin: 10px 0;

            input {
                margin: 0;
                padding: 10px;
                &[type="submit"] {
                    width: auto;
                    padding: 10px;
                    padding-bottom: 12px;
                    border-top-right-radius: 6px;
                    border-bottom-right-radius: 6px;
                    color: $white;
                    font-family: $font-title;
                    font-weight: 700;
                    cursor: pointer;
                    transform: translateX(-4px);
                    @include btn-pink; } } } }



    //tables
    table {
        width: 100%;
        text-align: left;
        font-family: $font-title;
        border-collapse: collapse;
        margin: 10px auto 20px;

        thead {
            background: $dark;
            color: $white; }

        tbody {
            tr {
                background: $white;
                &:hover {
                    background: darken($white, 15%); } } }

        th, td {
            padding: 5px 10px; }

        th {
            border: solid $black 1px; }
        td {
            border: solid darken($white, 15%) 1px;

            button {
                padding: 5px;
                border-radius: 4px;
                color: white;
                font-weight: 700;
                margin-right: 5px; }
            button:nth-of-type(1) {
                background: $pink;
                &:hover {
                    background: darken($pink, 10%); } }
            button:nth-of-type(2) {
                background: $blue;
                &:hover {
                    background: darken($blue, 10%); } }
            button:nth-of-type(3) {
                background: $red;
                &:hover {
                    background: darken($red, 10%); } } } } }
